import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Container from "react-bootstrap/Container";
import "bootstrap/dist/css/bootstrap.css";
import styles from "./sectionone.module.css";
import { Col, Row } from "react-bootstrap";
import { whatToExpectContent } from "../../helper/Helper";

const Causes = (props) => {
  let { data } = props;

  const ListItem = ({ data }) => {
    return (
      <li className={styles.sectionTwoListItem}>
        <span>{data?.title}</span>
        <p>{data?.text}</p>
      </li>
    );
  };

  const ListPara = ({ data }) => {
    return <p>{data}</p>;
  };


  return (
    <div className={styles.SectionTwo}>
      <div className={styles.SectionTwoInnerCont}>
        <div className={styles.sectionTwoHeader}>
          <div className={styles.sectionTwoHeaderTitle}>Causes:</div>
        </div>
        <div className={styles.sectionTwoUlWrapper}>
          {data?.map((item, index) => {
            if (item?.title == null) {
              return <ListPara data={item?.text} key={index} />;
            }
            if (item?.title) {
              return (
                <ul key={index}>
                  <ListItem data={item} key={index} />
                </ul>
              );
            }
          })}
        </div>
      </div>
    </div>
  );
};

export default Causes;
