import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Container from "react-bootstrap/Container";
import "bootstrap/dist/css/bootstrap.css";
import styles from "./sectionone.module.css";
import { Col, Row } from "react-bootstrap";
import { camelize } from "../../helper/Helper";

const SectionOneContent = (props) => {
  let {norisk, title, data, hide0, symptoms, seperateTitle } = props;
  let benefitData = data?.benefits

  if (norisk) {
    data["risks"] = [];
  }

  return (
    <div className={styles.contentWrapper}>
      {symptoms?.length > 0 && (
        <div className={styles.heading24}>
          {`${seperateTitle || "Symptoms of " + title}`}:
        </div>
      )}

      {symptoms?.length == 0 &&
        data?.slugText != null &&
        data?.causes?.length == 0 && (
          <div className={styles.paragraphOrderText}>{data?.slugText}</div>
        )}

      {symptoms?.map((item, index) => {
        if (hide0 && index == 0) {
          return;
        }
        return (
          <div key={index} className={styles.paragraphOrderText}>
            {camelize(item)}
          </div>
        );
      })}
      {benefitData?.length > 0 && (
        <div className={styles.heading24}>Benefits of {title}:</div>
      )}
      {benefitData?.map((item, index) => {
        return (
          <div key={index} className={styles.paragraphOrderText}>
            {camelize(item)}
          </div>
        );
      })}
      {data?.risks?.length > 0 && (
        <div className={styles.heading24}>{data?.risks[0]}:</div>
      )}
      {data?.risks?.map((item, index) => {
        if (index == 0) return;
        return (
          <div key={index} className={styles.paragraphOrderText}>
            {camelize(item)}
          </div>
        );
      })}
    </div>
  );
};

export default SectionOneContent;
