import React, { useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.css";
import styles from "../../../css/DetailPage/main.module.css";
import { Col, Row } from "react-bootstrap";
import { howItWorksContent } from "../../../helper/Helper";

const SectionThree = () => {
  const videoRef = useRef(0);
  const [isPlaying, setIsPlaying] = useState(false);

  const playVideo = () => {
    if (isPlaying) {
      videoRef.current.pause();
      setIsPlaying(false);
    } else {
      videoRef.current.play();
      setIsPlaying(true);
    }
  };

  return (
    <div className={styles.SectionThree}>
      <div className={styles.SectionThreeInnerCont}>
        <div className={styles.textureWrapper2} />
        <Row className={styles.SectionThreeRow}>
          <Col lg="6" className={styles.SectionThreeCol1}>
            <div className={styles.SectionThreeCol1Title}>How it Works?</div>
            <div className={styles.SectionThreeCol1BulletWrapper}>
              {howItWorksContent?.map((item, index) => {
                return (
                  <div key={index} className={styles.SectionThreeCol1Bullet}>
                    <div className={styles.SectionThreeCol1BulletNum}>
                      {index + 1}
                    </div>
                    <div className={styles.SectionThreeCol1BulletText}>
                      {item?.title}
                    </div>
                  </div>
                );
              })}
            </div>
          </Col>
          <Col lg="6" className={styles.SectionThreeCol2}>
            <div className={styles.SectionThreeCol2VideoWrapper}>
              {!isPlaying && (
                <div onClick={playVideo} className={styles.playButton}>
                  <img
                    src={require("../../../assets/play.png")}
                    className={styles.playIcon}
                  />
                </div>
              )}
              <video
                onTouchEnd={playVideo}
                onPause={() => setIsPlaying(false)}
                onPlay={() => setIsPlaying(true)}
                ref={videoRef}
                preload="auto"
                className={styles.SectionThreeCol2Video}
                controls
                poster={require("../../../assets/videoThumbnail2.png")}
              >
                <source src={require("../../../assets/video2.mp4")} />
              </video>
            </div>
          </Col>
        </Row>
      </div>
    </div>
  );
};

export default SectionThree;
