import React from "react";
import styles from "./footer.module.css";
import { footer_menu, sociallogo } from "../../../helper/Helper";
import { Col, Row } from "react-bootstrap";
import SocialLinks from "./SocialLinks";
import { getAuth, signOut } from "firebase/auth";
import app from "../../../env";

const Footer = () => {
  const LogOut = () => {
    const auth = getAuth(app)
    try {
      signOut(auth)
        .then(() => {
          // Sign-out successful.
        })
        .catch((error) => {
          // An error happened.
        });
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <div className={styles.footerSection}>
      <Row className={styles.footerSectionInnerCont}>
        <Col lg="4" className={styles.footerCol1}>
          <SocialLinks social />
        </Col>

        <Col lg="4" className={styles.footerCol2}>
          <img
            src={require("../../../assets/leaf.png")}
            className={styles.footerCol2Logo}
          />
        </Col>
        <Col lg="4" className={styles.footerCol3}>
          <Row className={styles.menuRow}>
            <Col lg="3" className={styles.menuItem}>
              Home
            </Col>
            <Col lg="3" className={styles.menuItem}>
              How it works
            </Col>
            <Col lg="3" className={styles.menuItem}>
              Help / FAQ
            </Col>
            <Col lg="3" className={styles.menuItem}>
              Privacy
            </Col>
          </Row>
        </Col>
      </Row>
    </div>
  );
};

export default Footer;
