import React, { useState } from "react";
import Button from "react-bootstrap/Button";
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import NavDropdown from "react-bootstrap/NavDropdown";
import Offcanvas from "react-bootstrap/Offcanvas";
import styles from "./navbar.module.css";
import { navbar } from "../../helper/Helper";
import { HashLink } from "react-router-hash-link";
import { FiLogOut } from "react-icons/fi";
import { getAuth, signOut } from "firebase/auth";
import app from "../../env";

const CustomNavbar = ({ to }) => {
  const LogOut = () => {
    const auth = getAuth(app);
    try {
      signOut(auth)
        .then(() => {
          // Sign-out successful.
        })
        .catch((error) => {
          // An error happened.
        });
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <Navbar
      key="md"
      expand="md"
      className={`${styles.navCont} bg-body-tertiary mb-3`}
    >
      <Container fluid className={styles.navInnerContainer}>
        <Navbar.Brand href="#" className={styles.brandLogo}>
          <div>
            <img
              className={styles.logoImg}
              src={require("../../assets/Logo-01.png")}
              alt=""
            />
          </div>
        </Navbar.Brand>
        <Navbar.Toggle
          className={styles.toggleBtn}
          aria-controls={`offcanvasNavbar-expand-md`}
        />
        <Navbar.Offcanvas
          id={`offcanvasNavbar-expand-md`}
          aria-labelledby={`offcanvasNavbarLabel-expand-md`}
          placement="end"
        >
          <Offcanvas.Header closeButton>
            <Offcanvas.Title id={`offcanvasNavbarLabel-expand-md`}>
              <div>
                <img
                  className={styles.logoImg2}
                  src={require("../../assets/Logo-01.png")}
                  alt=""
                />
              </div>
            </Offcanvas.Title>
          </Offcanvas.Header>
          <Offcanvas.Body className={styles.menuWrapper}>
            <Nav className="justify-content-end flex-grow-1 pe-3">
              {navbar?.map((item, index) => {
                let prod = `https://adlerware.net/CareXPS/#${item?.to}`;
                let dev = "/#" + item?.to;
                return (
                  <HashLink to={prod} className={styles.navBarItem} key={index}>
                    {item?.title}
                  </HashLink>
                );
              })}
              <HashLink
                md="1"
                className={[styles.navBarItem, { padding: 0 }]}
                onClick={LogOut}
              >
                <span className={styles.logoutText}>SignOut</span>
              </HashLink>
            </Nav>
            {/* <button className={styles.exploreButton}>Explore Treatments</button> */}
          </Offcanvas.Body>
        </Navbar.Offcanvas>
      </Container>
    </Navbar>
  );
};

export default CustomNavbar;
